import Vue from 'vue'
import Router from 'vue-router'
import Home from './Home.vue'
import Map from './pages/Map.vue'
import Chapter from './pages/Chapter.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/:chapter',
      name: 'Chapter',
      component: Chapter
    },
    {
        path: '/:name/:map',
        name: 'Map',
        component: Map
      }
  ]
})